body {
    margin: 10px;
}

div, h1, ul, li {
    font-family: sans-serif;
}

ul {
    margin-top: 0px;
}

p {
    text-align: justify;
    /* text-justify: inter-word; */
}

p.tabbedParagraph {
    text-align: justify;
    margin-top: 0px;
    margin-left: 50px;
}

p.tabbedParagraphParent {
    margin-bottom: 0px;
}

ul.menu {
    margin: 0px;
    padding: 0px;
    background-color: #863030;
}

ul.menu li {
    display: inline-block;
    margin: 0px;
    padding: 0px;
    position: relative;
}

a {
    display: inline-block;
    /* border-top: solid;
    border-width: 2px; */
    margin: 0px;
    padding: 20px;
    /* position: relative; */
    /*color: #f39a24;*/
    color: #fdb45f;
    text-decoration: none;
    font-weight: bold;
}

a:hover {
    background-color: #6b2525;
}

.active {
    background-color: #4e1414;
}

.menuItem {
    position: relative;
}

/* Show the dropdown menu on hover */

.menuItem:hover .dropdown-content {
    display: block;
}

/* Dropdown Content (Hidden by Default) */

.dropdown-content {
    display: none;
    position: absolute;
    background-color: #863030;
    width: auto;
    min-width: 160px;
    white-space: nowrap;
    /* box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2); */
    /* z-index: 1; */
}

/* Links inside the dropdown */

.dropdown-content a {
    display: block;
    padding: 10px;
}

.textLink {
    padding: 0px;
    color: #863030;
    font-weight: normal;
}

.textLink:hover {
    background-color: whitesmoke;
}

