    margin-left: 20px;
    float: right;
    max-width: 610px;
} */

/***********************************************************IMAGE CONTAINER - BEGIN************************************************************/

.imageContainer, .imageContainerLarge {
    margin-left: 20px;
    /* margin-top: 10px; */
    margin-bottom: 20px;
    float: right;
    clear: right;
}

.image, .imageLarge {
    margin: 0;
    padding: 0;
}

.imageDescription, .imageDescriptionLarge {
    text-align: center;
    font-weight: bold;
    word-wrap: break-word;
}

/*ImageContainer for small images (300px)*/

.image {
    max-width: 20vw;
    min-width: 20vw;
}

.imageDescription {
    max-width: 20vw;
}

/*ImageContainer for large images (600px)*/

.imageLarge {
    max-width: 25vw;
    min-width: 25vw;
}

.imageDescriptionLarge {
    max-width: 25vw;
}

/***********************************************************IMAGE CONTAINER - END ************************************************************/

/*************************************************************SLIDING MENU - BEGIN************************************************************/

.slidingMenuInvisible, .slidingMenuVisible {
    /* border-style: solid; */
    /* border-width: 1px; */
    /* border-color: #fdb45f; */
    /* padding-top: 60px; */

    height: 100%;
    position: fixed;
    z-index: 1;
    top: 0;
    left: 0;
    background-color: #4e1414;
    /* overflow-x: hidden; */
    transition: 0.3s;
}

.slidingMenuInvisible {
    width: 0;
}

.slidingMenuVisible {
    width: 250px;
}

.openButton {
    margin-left: 5px;
    margin-right: 5px;
    border-style: solid;
    border-width: 1px;
    padding: 0px;
    color: #fdb45f;
    font-size: 20px;
    cursor: pointer;
    vertical-align: center;
}

.openButton:hover {
    /* background-color: #6b2525;  */
    background-color: black; 
}

.closeButton {
    position: absolute;
   /*  top: 0;
    right: 0px; */
    /* margin-top: 5px;
    margin-right: 5px;
    margin-bottom: 5px;
    padding: 2px; */
    right: 5px;

    font-size: 36px;
    color: #fdb45f;
    cursor: pointer;
}

/* .closeButton:hover {
    background-color: #863030; 
} */

.slidingMenuContent {
    position: absolute;
    top: 50;
    right: 10px;
    /* left: 10px; */

    margin-top: 30px;
    padding-left: 10px;

    color: #fdb45f;
    /* font-size: 36px; */
    /* margin-left: 50px;  */
}

.slidingMenuParagraph {
    text-align: left;
}

.wikiLink {
    display: block;
    padding: 10px;
}

/*************************************************************SLIDING MENU - END ************************************************************/